import { i18n, lan } from '../../unit/const'
const DF = i18n.point[lan]
const ZDF = i18n.highestScore[lan]
const SLDF = i18n.lastRound[lan]
let Point = {
  timeout: null
}
import Number from '../number/index.vue'
export default {
  props: ['cur', 'point', 'max', 'realScore'],
  mounted() {
    this.onChange(this.$props)
  },
  components:{
    Number
  },
  data() {
    return {
      label: '',
      number: 0
    }
  },
  watch:{
    $props:{
      deep:true,
      handler(nextProps){
        this.onChange(nextProps);
      }
    }
  },
  methods: {
    onChange({cur, point, max, realScore} ) {
      clearInterval(Point.timeout)

      if (cur) {
        // 
        // this.label = point >= max ? ZDF : DF
        this.label = DF  // terry:
        // this.number = point
        this.number = realScore  // 
      } else {
        // 
        const toggle = () => {
          // 
          // this.label = ZDF
          // this.number = point
          // Point.timeout = setTimeout(() => {
          //   this.label = ZDF
          //   this.number = max
          //   Point.timeout = setTimeout(toggle, 3000)
          // }, 3000)

          // terry 
          this.label = DF
          this.number = realScore

        }

        if (point !== 0) {
          // , 
          toggle()
        } else {
          this.label=DF  // ZDF
          this.number=realScore  // max
        }
      }
    }
  }
}



